@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;1,400;1,800&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* theming */
:root {
  /* colors */
  --white: #fff;
  --black: #000;

  /* theme colors */
  --navbar-background-color: transparent;
  --navbar-scroll-background-color: var(--black);
  --logo-color: var(--white);
  --logo-outline-color: var(--black);
  --footer-background: var(--black);
  --footer-text: var(--white);
}

@use './src/assets/colors'

.landing-page{background-color:var(--primary-color)}

.navbar{position:fixed;display:flex;width:100%;height:50px;align-items:center;background-color:var(--navbar-background-color);transition:background-color 500ms}.navbar__logo{padding:0 16px;font-size:24px;text-transform:uppercase;color:var(--logo-color);font-weight:800;text-shadow:-1px -1px 0 var(--logo-outline-color),1px -1px 0 var(--logo-outline-color),-1px 1px 0 var(--logo-outline-color),1px 1px 0 var(--logo-outline-color)}

.footer{margin-top:48px;padding:24px 0;background-color:var(--footer-background);color:var(--footer-text)}

